.user__about {
    .userNameWithStatus {
        .user__name {
            margin: 0;
            margin-right: 1rem;
            font-size: 1.5rem;
            line-height: 1.5; } }
    .user__description {
        width: 80%;
        display: grid;
        grid-template-columns: repeat(auto-fill, 15rem);
        gap: .5rem 1rem;
        .user__item-webSites {
            overflow: hidden;
            display: block;
            max-width: 10%; }
        .user__item-about {
            padding: 0;
            margin: 0;
            font-size: .875rem;
            line-height: 1rem;
            .webSite {
                margin: 0;
                padding: 0 2rem;
                list-style: none;
                line-height: 1.3; } } } }
.user__title {
    font-size: 1.2rem;
    margin: 1rem 0;
    padding: 0; }
